import React, { useState, useEffect } from 'react';
import { Button, Nav } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { menuItems } from '../../constants/Portfolio';
import { updateMenus } from '../functions/Helpers';

const MainMenu = (props) => {
  const [currentMenuItemIndex, setCurrentMenuItemIndex] = useState(0);
  const [visibleMenuItems, setVisibleMenuItems] = useState(3); // Number of initially visible menu items (changed to 3)

  useEffect(() => {
    // Update the currentMenuItemIndex when the component mounts or menuItems change
    setCurrentMenuItemIndex(0);
  }, [menuItems]);

  function scrollTo(event) {
    const menuItem = event.target.innerText;
    const albumDiv = document.getElementById(menuItem);
    if (albumDiv) {
      albumDiv.scrollIntoView({ behavior: 'smooth' });
      updateMenus(menuItem, setCurrentMenuItemIndex);
      setCurrentMenuItemIndex(menuItems.indexOf(menuItem));
    }
  }

  function handleDownArrowClick() {
    // Increase the number of visible menu items by 2
    setVisibleMenuItems((prevVisibleMenuItems) => prevVisibleMenuItems + 2);
  }

  const menuButtons = menuItems.slice(0, visibleMenuItems).map((menuItem, index) => (
    <Link key={index}>
      <Button
        id={`menu-item-${index}`}
        onClick={scrollTo}
        variant="link"
        className="menu-button" // Add this class
        style={{
          color: '#dddd',
          textAlign: 'left',
          paddingLeft: 0,
        }}
      >
        {menuItem}
      </Button>
    </Link>
  ));

  const hasMoreMenuItems = menuItems.length > visibleMenuItems; // Check if there are more menu items to display

  return (
    <div className="sidebarmenu" style={{ display: `${props.display}` }}>
      <Nav className="flex-column">
        {menuButtons}
        {hasMoreMenuItems && (
          <Button
            id="next-menu-arrow-down"
            onClick={handleDownArrowClick}
            variant="link"
            style={{
              color: '#dddd',
              textAlign: 'left',
              paddingLeft: 0,
            }}
          >
            +{/* Change the down arrow to a plus sign */}
          </Button>
        )}
      </Nav>
    </div>
  );
};

export default MainMenu;
