import React, { useState, useCallback, useMemo, useEffect } from 'react';
import Footer from "../shared/Footer";
import HeroImage from "../shared/HeroImage";
import PortfolioButton from "../shared/PortfolioButton";
import { Link } from 'react-router-dom';
import CustomGallery from "./CustomGallery";
import BurgerMenu from './BurgerMenu'; // Import the BurgerMenu component

function Mobile({ gifSrc }) {
  
  //styles
  const gifContainerStyle = {
    position: "fixed",
    left: "50%",
    bottom: "35%",
    width: "100%",
    transform: "translateX(-50%)",
    textAlign: "center",
  };

  const footerStyle ={
    display:"block",
    position:"fixed", 
    bottom:"10%", 
    displaycameratag:"none",
  };

  const portfolioButtonStyle={
    fontSize: 'large',
    color: '#dddd',
    position: 'fixed',
    fontWeight: 700,
    left: '50%',
    transform: 'translateX(-50%)',
    bottom: '50%',
  }

  const burgerMenuToggleStyle = {
    position: 'fixed',
    bottom: '0',  // Position at the bottom
    background: 'black',
    opacity: '85%',
    width: '100%',
    padding: '2%',
    visibility: 'hidden',
  };


  

  const [showGallery, setShowGallery] = useState(false);
  const [isBurgerMenuOpen, setIsBurgerMenuOpen] = useState(false); // Change the initial state to false

  function enterPortfolio() {
    setShowGallery(true);
    var burgertoogle = document.getElementById("burger-menu-toggle-id");
    burgertoogle.style.visibility = 'visible'; // Use 'style.visibility' to set CSS property
  }

  function toggleBurgerMenu() {
    setIsBurgerMenuOpen(!isBurgerMenuOpen);
  }

    // Function to close the burger menu
    function closeBurgerMenu() {
      setIsBurgerMenuOpen(false);
    }
     // Event listener for clicks outside the burger menu
  const handleClickOutside = useCallback((e) => {
    if (isBurgerMenuOpen) {
      const burgerMenuToggle = document.querySelector('.burger-menu-toggle');
      if (burgerMenuToggle && !burgerMenuToggle.contains(e.target)) {
        closeBurgerMenu();
      }
    }
  }, [isBurgerMenuOpen]);

  // Add the click event listener when the component mounts
  useEffect(() => {
    document.addEventListener('click', handleClickOutside);
    // Clean up the event listener when the component unmounts
    return () => {
      document.removeEventListener('click', handleClickOutside);
    };
  }, [handleClickOutside]);


return (
    <div className="MobileTablet">
       {/* Burger menu toggle button */}
       <div id="burger-menu-toggle-id" className="burger-menu-toggle" style={burgerMenuToggleStyle} onClick={toggleBurgerMenu}>
        <span>&#9776;</span> {/* Add a burger menu icon here */}
      </div>

      {/* Burger menu component */}
      <BurgerMenu isOpen={isBurgerMenuOpen} onClose={toggleBurgerMenu} />

      {showGallery ? null : (
        <HeroImage gifSrc="/home/homepagehero.gif" gifContainerStyle={gifContainerStyle}/>
      )}
      {showGallery ? (
        <CustomGallery />
      ) : (
        <>
          <Link onClick={enterPortfolio}>
            <PortfolioButton portfolioButtonStyle={portfolioButtonStyle} />
          </Link>
          <Footer footerStyle={footerStyle} displaycameratag="none"/>
        </>
      )}
    </div>
  );
}

export default Mobile;