import React, { useState, useRef } from 'react';
import { Link } from 'react-router-dom';
import { menuItems } from '../../constants/Portfolio';

const BurgerMenu = ({ isOpen, onClose }) => {
  // Define styles for the burger menu
  const menuStyle = {
    position: isOpen ? 'fixed' : 'absolute',
    top: isOpen ? '0' : '-100%',
    right: isOpen ? '0' : '-100%', // Align to the top right
    width: '55%',
    height: '100%',
    backgroundColor: 'black',
    opacity: '95%',
    zIndex: 999,
    paddingTop:'50%',
    transition: 'bottom 0.3s ease-in-out',
    
  };
  

  // Define styles for individual menu items
  const menuItemStyle = {
    display: 'block',
    padding: '10px',
    textDecoration: 'none',
    color: 'rgba(221, 221, 221, 0.867)', // Customize the text color
    fontSize:'0.8rem'
    };

  // Function to scroll to a specific image
  const scrollToImage = (category) => {
    const albumDiv = document.getElementById(category);
    albumDiv.scrollIntoView({ behavior: 'smooth' });
  };
  return (
<div style={menuStyle} className={`burger-menu ${isOpen ? 'sticky-menu' : ''}`}>
      {menuItems.map((menuItem, index) => (
        <Link
          key={index}
          style={menuItemStyle}
          onClick={() => {
            onClose();
            scrollToImage(menuItem);
          }}
        >
          {menuItem}
        </Link>
      ))}
    </div>
  );
};

export default BurgerMenu;
