import React, { useState, useCallback, useMemo } from 'react';
import '../Carrousel.css';
import { images } from '../../constants/Portfolio';
import Gallery from 'react-photo-gallery';
import Carousel, { Modal, ModalGateway } from "react-images";

function CustomGallery({ galleryStyle }) {
  const [currentImage, setCurrentImage] = useState(0);
  const [viewerIsOpen, setViewerIsOpen] = useState(false);

  const openLightbox = useCallback((event, { index }) => {
    setCurrentImage(index);
    setViewerIsOpen(true);
  }, []);

  const closeLightbox = useCallback(() => {
    setCurrentImage(0);
    setViewerIsOpen(false);
  }, []);

  const photos = useMemo(() => {
    const result = [];
 
    function getId(className, id) {
      if (className && className.includes("first-pic")) {
        return id;
      }
      return null; // Return null if the className doesn't contain "first-pic"
    }
    
    Object.keys(images).forEach((key) => {
      const imagesArray = images[key];
      for (let i = imagesArray.length - 1; i >= 0; i--) {
        const image = {
          src: '',
          width: 0,
          height: 0,
          category: '',
          id:'',
        };

        if (imagesArray[i][1].includes('youtube-video')) {
          image.src = imagesArray[i][0];
          image.width = 560;
          image.height = 315;
          image.category = imagesArray[i].key
        } else {
          image.src = 'gallery/'+key + '/thumbs/' + imagesArray[i][0];
          image.width = imagesArray[i][2];
          image.height = imagesArray[i][3];
          image.className = imagesArray[i][1];
          image.category = key
          image.id = getId(imagesArray[i][1],key)
        }

        result.push(image);
      }
    });

    return result;
  }, []);

  function contextMenu(e) {
    e.preventDefault();
    const year = new Date().getFullYear();
    alert(`This photo is Copyright © ${year}\nGabriel Patrício. All rights reserved`);
  }

  function preventDragHandler(e) {
    e.preventDefault();
  }

  return (
    <div style={galleryStyle} className="containerMobile" id="containerMobile">
      <div className="contentMobile" onContextMenu={contextMenu} onDragStart={preventDragHandler}>
        <div>
        {/* <Gallery photos={photos} onClick={openLightbox} columns={3} direction='row' margin={0}/> */}
          <Gallery photos={photos} columns={3} direction='row' margin={0}/>
          <ModalGateway>
            {viewerIsOpen ? (
              <Modal onClose={closeLightbox}>
                <Carousel
                  currentIndex={currentImage}
                  views={photos.map(x => ({
                    ...x,
                    srcset: x.srcSet,
                    caption: x.title
                  }))}
                />
              </Modal>
            ) : null}
          </ModalGateway>
        </div>
      </div>
    </div>
  );
}

export default CustomGallery;
